<template>
	<button @mouseenter="activation = true" 
			:class="{'active-indicators':activation}">INDICATORS</button>

	<div 
		:class="{'container-list':true,'active':activation}"  
		@mouseleave="activation = false">
		<ul>
			<li>
				<input type="checkbox" id="checkboxBB" v-model="indicadores.bb" />
				<label for="checkboxBB" 
					data-tooltip="Bandas de Bollinger"
					data-position="right"
				>BB</label>
			</li>
			<li>
				<input type="checkbox" id="checkboxDT" v-model="indicadores.dt" />
				<label for="checkboxDT"
					data-tooltip="Detalles por vela"
					data-position="right"
					>DT</label>
			</li>
			<li>
				<input type="checkbox" id="checkboxFA" v-model="indicadores.fa" />
				<label for="checkboxFA"
					data-tooltip="Fibonacci Automatico"
					data-position="right"
					>FA</label>
			</li>
			<li>
				<input type="checkbox" id="checkboxPB" v-model="indicadores.pb" />
				<label for="checkboxPB"
					data-tooltip="PullBack"
					data-position="right"
				>PB</label>
			</li>
			<li>
				<input type="checkbox" id="checkboxHA" v-model="indicadores.ha" />
				<label for="checkboxHA"
					data-tooltip="Heikin Ashi"
					data-position="right"
					>HA</label>
			</li>
			<li>
				<input type="checkbox" id="checkboxATR" v-model="indicadores.atr" />
				<label for="checkboxATR">ATR</label>
			</li>
			<li>
				<input type="checkbox" id="checkboxPSAR" v-model="indicadores.psar" />
				<label for="checkboxPSAR"
					data-tooltip="Parabolic Stop and Reverse"
					data-position="right"
					>PSAR</label>
			</li>
			<li>
				<input type="checkbox" id="checkboxEMA10" v-model="indicadores.ema10" />
				<label for="checkboxEMA10">ema 10</label>
			</li>
			<li>
				<input type="checkbox" id="checkboxEMA21" v-model="indicadores.ema21" />
				<label for="checkboxEMA21">ema 21</label>
			</li>
			<li>
				<input type="checkbox" id="checkboxEMA55" v-model="indicadores.ema55" />
				<label for="checkboxEMA55">ema 55</label>
			</li>
			<li>
				<input type="checkbox" id="checkboxEMA89" v-model="indicadores.ema89" />
				<label for="checkboxEMA89">ema 89</label>
			</li>
			<li>
				<input type="checkbox" id="checkboxEMA100" v-model="indicadores.ema100" />
				<label for="checkboxEMA100">ema 100</label>
			</li>
			<li>
				<input type="checkbox" id="checkboxEMA200" v-model="indicadores.ema200" />
				<label for="checkboxEMA200">ema 200</label>
			</li>
			<li>
				<input type="checkbox" id="checkboxEMA233" v-model="indicadores.ema233" />
				<label for="checkboxEMA233">ema 233</label>
			</li>
			
		</ul>
		
	</div>
</template>
<script>

import {
	defineComponent,
	ref,
	reactive,
	onMounted,
	watch,
	nextTick,
	onUnmounted,
	computed,
	toRefs,
	defineEmits
} from "vue";

export default {
	name:"OptionsListChecks",
	emits: [
		'changeBB',
		'changeDT',
		'changeFA',
		'changePB',
		'changeHA',
		'changeATR',
		'changePSAR',
		'changeEma10',
		'changeEma21',
		'changeEma55',
		'changeEma89',
		'changeEma100',
		'changeEma200',
		'changeEma233'
		],
	setup(props,{emit}) {

			const activation = ref(false);
			const indicadores = reactive({
				bb:true,
				dt:false,
				fa:false,
				pb:false,
				ha:false,
				atr:false,
				psar:false,
				ema10:false,
				ema21:false,
				ema55:false,
				ema89:false,
				ema100:false,
				ema200:false,
				ema233:false
			});

			// send updates
			watch(() => ({ ...indicadores.bb }),
				function () {
					emit('changeBB', indicadores.bb);
				}
			);

			watch(() => ({ ...indicadores.dt }),
				function () {
					emit('changeDT', indicadores.dt);
				}
			);

			watch(() => ({ ...indicadores.fa }),
				function () {
					emit('changeFA', indicadores.fa);
				}
			);

			watch(() => ({ ...indicadores.pb }),
				function () {
					emit('changePB', indicadores.pb);
				}
			);

			watch(() => ({ ...indicadores.ha }),
				function () {
					emit('changeHA', indicadores.ha);
				}
			);

			watch(() => ({ ...indicadores.atr }),
				function () {
					emit('changeATR', indicadores.atr);
				}
			);

			watch(() => ({ ...indicadores.psar }),
				function () {
					emit('changePSAR', indicadores.psar);
				}
			);

			watch(() => ({ ...indicadores.ema10 }),
				function () {
					emit('changeEma10', indicadores.ema10);
				}
			);

			watch(() => ({ ...indicadores.ema21 }),
				function () {
					emit('changeEma21', indicadores.ema21);
				}
			);

			watch(() => ({ ...indicadores.ema55 }),
				function () {
					emit('changeEma55', indicadores.ema55);
				}
			);

			watch(() => ({ ...indicadores.ema89 }),
				function () {
					emit('changeEma89', indicadores.ema89);
				}
			);

			watch(() => ({ ...indicadores.ema100 }),
				function () {
					emit('changeEma100', indicadores.ema100);
				}
			);

			watch(() => ({ ...indicadores.ema200 }),
				function () {
					emit('changeEma200', indicadores.ema200);
				}
			);


			watch(() => ({ ...indicadores.ema233 }),
				function () {
					emit('changeEma233', indicadores.ema233);
				}
			);

			return {
				activation,
				indicadores
			}
		}
}
	
</script>
