  async function comprarSubscription() {
    if (contractSubscription) {
      const options = {
        value: ethers.utils.parseEther(costoSubscription.value),
      };
      const valorWei = await options.value.toString();
      // const reciept = await contract.buyPunk(1001, options);

      let tx = await contractSubscription.comprar(options);

      if (tx.hash) {
        const Toast = Swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 3000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener("mouseenter", Swal.stopTimer);
            toast.addEventListener("mouseleave", Swal.resumeTimer);
          },
        });
        const mascaraTx =
          tx.hash.substr(0, 5) + "....." + tx.hash.substr(-5, 5);
        Toast.fire({
          icon: "success",
          title: `hash ${mascaraTx}`,
        });
        await searchWallet();

        let checkTransaccion = setInterval(async () => {
          const response = await fetch(
            `${URLBASE_API_BSCSCAN}?module=transaction&action=gettxreceiptstatus&txhash=${tx.hash}&apikey=YourApiKeyToken`
          ).then((d) => d.json());

          let t = await contractSubscription.getTickets();
          let subs = await contractSubscription.getSubscripcionesDisponibles();

          tickets.value = Number(t.toString());
          subsDisponibles.value = subs.toString();

          await searchWallet();


          if (response.status == "1" && response.message == "OK") {
            clearInterval(checkTransaccion);
          }
        }, 5000);
      }
    }
  }

