	const startBD = async () => {
		dataBase = await indexDB.open("topacio-trade", 1);

		dataBase.onupgradeneeded = (e) => {
			// indexDB.createTable('config');
			active = dataBase.result;

			const obj = active.createObjectStore("config", {
				keyPath: "id",
				autoIncrement: true,
			});
			obj.createIndex("by_name", "name", { unique: true });
			obj.createIndex("by_data", "data", { unique: false });
			obj.createIndex("binance", "binance", { unique: true });

			console.log("onupgradeneeded", { e });
		};

		dataBase.onsuccess = (e) => {
			active = dataBase.result;
			console.log("onsuccess", { e });
		};

		dataBase.onerror = (e) => {
			console.log("onerror", { e });
		};
	};

